#kontakt{
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    height: 88vh;
}
#container1{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 20px 100px;
}
#container1::after{
    content: '';
    position: absolute;
    left: 0;
    top:0;
    width: 100%;
    height:100%;
    background: url("./xps-ocAuPlfZXEc-unsplash.jpg") no-repeat;
    background-size: cover;
    z-index: -1;
    filter: blur(10px);
}
#contact-box{
    max-width: 850px;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: #fff;
    box-shadow: 0px 0px 19px 5px rgba(0,0,0,0.19);
}
#left{
    height: 100%;
    background: url("./xps-ocAuPlfZXEc-unsplash.jpg") no-repeat;
    background-size: cover;
}
#right{
    padding: 25px 40px;
}
#right h2{
    position:relative;
    padding-bottom: 10px;
    margin-bottom: 10px;
}
#right h2::after{
    content: '';
    position:absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    height: 4px;
    width:50px;
    border-radius:2px ;
    background-color:#2ecc71;
}
.field{
    width: 100%;
    padding: 0.5rem 1rem;
    outline: none;
    border: 2px solid rgba(0, 0, 0, 0);
    background-color:  rgba(230, 230, 230, 0.6);
    font-size: 1.1rem;
    margin-bottom: 22px;
    transition:.3s;
}
.field:hover{
    background-color: rgba(0, 0, 0, 0.1);
}
.field:focus{
    background-color: #fff;
    border:2px solid rgba(30, 85, 250, 0.47);
}
.area{
    min-height: 150px;
}

.btn{
    width: 100%;
    padding: 0.5rem 1rem;
    font-size: 1.1rem;
    background-color: #2ecc71;
    cursor:pointer;
    outline: none;
    border:none;
    transition: .3s;
}
.btn:hover{
    background-color: #27ae60;
    color:#fff;
}
@media screen and (max-width:880px) {
    #contact-box{
        grid-template-columns: 1fr;
    }
    #left{
        height: 200px;
    }
}
#head1LogIn{
    margin-top: 50px;
}
#head4LogIn{
    margin-top: 30px;
    margin-bottom: 20px;
    font-size: larger;
}
#head4LogIn a{
    text-decoration: none;
    color: #0d6dfd;
    transition: 0.3s;
}
#head4LogIn a:hover{
    text-decoration: none;
    color: #6da6fc;
}